$white: #fff;
$black: #000;
$main-accent: #060b35;
$seceondary: #616070;
$dark-white: #f3f3f3;
$blue-accent: #00acdc;
$dark-blue: #34568b;
$card-width: 200px;
$card-contnet-width: 166px;
$card-border-width: 1px;
$card-padding: calc(
  ($card-width - $card-contnet-width) / 2 - $card-border-width
);
$star-size: 16px;
$star-margin: 4px;
$stars-width: $star-size * 5 + $star-margin * 4;
