.page-content {
  flex-direction: column;
  max-width: 100vw;
  min-height: 100vh;
  font-family: Roboto, sans-serif;
  display: flex;
}

.page-content__main-content {
  justify-content: center;
  align-items: center;
  display: flex;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000c;
}

.header__link {
  align-items: center;
  display: flex;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  height: 60px;
  margin: 0 10px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
  cursor: pointer;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:first-child > * {
  margin-left: 0;
}

.nav__item:last-child > * {
  margin-right: 0;
}

.is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

.catalog {
  gap: 45px 48px;
  padding: 50px 40px;
  display: grid;
}

@media screen and (width >= 488px) and (width <= 768px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media screen and (width >= 768px) and (width <= 1024px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media screen and (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 200px;
  padding: 32px 16px 17px;
  transition: all .3s;
  display: flex;
}

.card__photo {
  width: 160px;
  height: 134px;
}

.card__name {
  color: #060b35;
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  align-self: flex-start;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  margin-top: 16px;
  line-height: 14px;
}

.card__price {
  margin-top: 24px;
}

.card__buy {
  margin-top: 16px;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__name {
  color: #34568b;
}

.buy {
  width: 100%;
}

.buy__button {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
}

.buy__button:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.price {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  line-height: 18px;
  display: flex;
}

.price__text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
}

.price__value {
  color: #060b35;
  font-size: 16px;
  font-weight: 400;
}

.reviews {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.reviews__text {
  font-size: 10px;
  font-weight: 400;
}

.stars {
  width: 96px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

* {
  box-sizing: border-box;
}

